// TODC Bootstrap functions
//
// Utility mixins and functions for evalutating source code across our variables, maps, and mixins.

@function btn-gm-default-color-map($color) {
  // stylelint-disable
  $btn-color-map: ();
  $btn-color-map: map-merge((
    "background":         $color,
    "border-color":       transparent,
    "start-color":        $color,
    "end-color":          $color,
    "hover-border-color": darken($color, 18%),
    "hover-start-color":  $color,
    "hover-end-color":    darken($color, 5%),
    "active-start-color": $color,
    "active-end-color":   darken($color, 10%)
  ), $btn-color-map);
  // stylelint-enable

  @return $btn-color-map;
}

@function btn-gm-color-map($btn-color) {
  @return map-get($btn-gm-colors, $btn-color);
}

@function btn-gm-color-key($btn-gm-color-map, $key) {
  @return map-get($btn-gm-color-map, $key);
}
