//
// Color system
//

// stylelint-disable
$white:    #fff !default;
$gray-50:  #fafafa;
$gray-100: #f5f5f5;
$gray-200: #eeeeee;
$gray-300: #e0e0e0;
$gray-400: #bdbdbd;
$gray-500: #9e9e9e;
$gray-600: #757575;
$gray-700: #616161;
$gray-800: #424242;
$gray-900: #212121;
$black:    #000 !default;

$grays: ();
$grays: map-merge((
  "50":  $gray-50,
  "100": $gray-100,
  "200": $gray-200,
  "300": $gray-300,
  "400": $gray-400,
  "500": $gray-500,
  "600": $gray-600,
  "700": $gray-700,
  "800": $gray-800,
  "900": $gray-900
), $grays);

$blue:    #4285f4;
$indigo:  #3f51b5;
$purple:  #9c27b0;
$pink:    #e91e63;
$red:     #f44336;
$orange:  #ff9800;
$yellow:  #ffeb3b;
$green:   #4caf50;
$teal:    #009688;
$cyan:    #00bcd4;

$colors: ();
$colors: map-merge((
  "blue":       $blue,
  "indigo":     $indigo,
  "purple":     $purple,
  "pink":       $pink,
  "red":        $red,
  "orange":     $orange,
  "yellow":     $yellow,
  "green":      $green,
  "teal":       $teal,
  "cyan":       $cyan,
  "white":      $white,
  "gray":       $gray-500,
  "gray-dark":  $gray-800
), $colors);

$primary:       $blue !default;
$secondary:     $white !default;
$success:       $green !default;
$info:          $cyan !default;
$warning:       $orange !default;
$danger:        $red !default;
$light:         $gray-100 !default;
$dark:          $gray-900 !default;

$theme-colors: ();
$theme-colors: map-merge((
  "primary":    $primary,
  "secondary":  $secondary,
  "success":    $success,
  "info":       $info,
  "warning":    $warning,
  "danger":     $danger,
  "light":      $light,
  "dark":       $dark
), $theme-colors);


// Brand Colors
//
// v3 colors
$brand-primary: $blue !default;
$brand-success: $success !default;
$brand-info:    $info !default;
$brand-warning: $warning !default;
$brand-danger:  #d14836 !default;

$brand-colors: ();
$brand-colors: map-merge((
  "primary":    $brand-primary,
  "secondary":  $secondary,
  "success":    $brand-success,
  "info":       $brand-info,
  "warning":    $brand-warning,
  "danger":     $brand-danger,
  "light":      $light,
  "dark":       $dark
