//
// Alternate buttons
//

// @if $enable-custon-btn {
// }

@each $color, $value in $theme-colors {
  .custom-btn-#{$color} {
    @include button-variant($background: $value, $border: $value);
  }
}

@each $color, $value in $theme-colors {
  .custom-btn-outline-#{$color} {
    @include button-outline-variant($color: $value);
  }
}

